@charset "UTF-8";
/* --------------------------------
 * base
 * -------------------------------- */
body {
  color: #333;
  font-weight: bold;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", "Meiryo", sans-serif; }

*, *::before, *::after {
  box-sizing: border-box; }

a:link, a:visited, a:hover, a:active {
  text-decoration: none; }

li {
  list-style: none; }

li a {
  color: #333; }

img {
  display: block;
  width: 100%; }

p {
  font-size: 16px;
  line-height: 2; }

.nav .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  width: calc(100% - 15px);
  margin: 0 auto; }

.nav .logo {
  background: url("./images/logo.png") no-repeat center center;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  max-width: 180px;
  height: 40px; }

.nav .navbar {
  display: none; }

/* Hamburger */
#nav-drawer {
  position: relative; }

/*チェックボックス等は非表示に*/
.nav-unshown {
  display: none; }

/*アイコンのスペース*/
#nav-open {
  display: inline-block;
  width: 30px;
  height: 22px;
  vertical-align: middle; }

/*ハンバーガーアイコンをCSSだけで表現*/
#nav-open span, #nav-open span:before, #nav-open span:after {
  position: absolute;
  height: 3px;
  /*線の太さ*/
  width: 25px;
  /*長さ*/
  border-radius: 3px;
  background: #555;
  display: block;
  content: '';
  cursor: pointer; }

#nav-open span:before {
  bottom: -8px; }

#nav-open span:after {
  bottom: -16px; }

/*閉じる用の薄黒カバー*/
#nav-close {
  display: none;
  /*はじめは隠しておく*/
  position: fixed;
  z-index: 99;
  top: 0;
  /*全体に広がるように*/
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0;
  transition: .3s ease-in-out; }

/*中身*/
#nav-content {
  overflow: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  /*最前面に*/
  width: 90%;
  /*右側に隙間を作る（閉じるカバーを表示）*/
  max-width: 330px;
  /*最大幅（調整してください）*/
  height: 100%;
  background: #fff;
  /*背景色*/
  transition: .3s ease-in-out;
  /*滑らかに表示*/
  transform: translateX(-105%);
  /*左に隠しておく*/ }

/*チェックが入ったらもろもろ表示*/
#nav-input:checked ~ #nav-close {
  display: block;
  /*カバーを表示*/
  opacity: .5; }

#nav-input:checked ~ #nav-content {
  transform: translateX(0%);
  /*中身を表示（右へスライド）*/
  box-shadow: 6px 0 25px rgba(0, 0, 0, 0.15); }

#nav-content {
  padding: 20px; }

.drawerList {
  border-top: 1px solid #555; }

.drawerItem a {
  display: block;
  border-bottom: 1px solid #555;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px; }

/* Hamburger end */
.hero {
  background: url("./images/heroBg.jpg") center center/cover;
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .hero .heroTl {
    color: #fff;
    font-size: 36px;
    line-height: 1.5;
    margin-bottom: 10px; }
  .hero .heroTxt {
    color: #fff;
    font-size: 16px; }

.blog {
  padding: 60px 0 88px; }
  .blog .secTl {
    font-size: 32px;
    text-align: center;
    margin-bottom: 50px; }
  .blog .boxWrap {
    max-width: 500px;
    width: calc(100% - 30px);
    margin: 0 auto 46px; }
  .blog .box {
    box-shadow: 0px 3px 6px #000029;
    margin-top: 25px; }
  .blog .box img {
    margin-bottom: 10px; }
  .blog .blogTl {
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 36px;
    padding: 0 20px; }
  .blog .blogDate {
    font-size: 16px;
    padding: 0 20px 20px; }
  .blog .btn a {
    border-radius: 5px;
    box-shadow: 0px 3px 6px #000029;
    font-size: 18px;
    text-align: center;
    display: block;
    margin: 0 auto;
    padding: 17px 0;
    max-width: 310px;
    height: 52px; }

.contact {
  background: url("./images/footerBg.jpg") center center/cover;
  height: auto;
  padding: 60px 0 44px; }
  .contact .container {
    width: calc(100% - 20px);
    margin: 0 auto; }
  .contact .contactTl {
    color: #fff;
    font-size: 36px;
    text-align: center;
    margin-bottom: 25px; }
  .contact p {
    color: #fff;
    text-align: center;
    margin-bottom: 50px; }
  .contact .btn a {
    color: #fff;
    background: #83032A;
    border-radius: 5px;
    box-shadow: 0px 3px 6px #000029;
    text-align: center;
    display: block;
    max-width: 210px;
    height: 54px;
    margin: 0 auto 44px;
    padding: 19px 25px; }

.copyLight {
  color: #fff;
  background: #333333;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  padding: 32px; }

@media (min-width: 768px) {
  .brsm {
    display: none; }
  .nav .container {
    max-width: 980px;
    padding: 20px 0; }
  .nav .navbar {
    display: flex; }
    .nav .navbar .navItem {
      margin-left: 20px; }
  .nav .spMenu {
    display: none; }
  .hero {
    height: 551px; } }

@media (min-width: 980px) {
  .nav .container {
    max-width: 980px; }
  .blog .boxWrap {
    display: flex;
    justify-content: space-between;
    width: auto;
    max-width: 980px; }
  .blog .box {
    flex-basis: 310px;
    height: 336px;
    position: relative; }
  .blog .blogDate {
    position: absolute;
    bottom: 0; } }
